<template>
  <div>
    <div class="screenFull">
      <div class="siginInForm">
        <div class="siginInTitle">
          <img src="../assets/log2.png" alt />
        </div>
        <Form
          ref="formCustom"
          :model="formCustom"
          :rules="ruleCustom"
          :label-width="0"
          class="form-content"
        >
          <FormItem prop="loginid" class="form-item">
            <Input prefix="ios-contact" v-model="formCustom.loginid" placeholder="请输入登录账号" />
          </FormItem>
          <FormItem prop="password" class="form-item">
            <Input
              type="password"
              prefix="md-lock"
              v-model="formCustom.password"
              placeholder="请输入登录密码"
            ></Input>
          </FormItem>
          <Row class="flexCenter btnBox">
            <Button type="primary" @click="handleSubmit('formCustom')">登录</Button>
            <!-- <Button @click="handleReset('formCustom')" style="margin-left: 8px">重置</Button> -->
            <!-- <Button @click="ceshi" style="margin-left: 8px">测试</Button> -->
          </Row>
        </Form>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  created() {
    let that = this;
    document.onkeypress = function(e) {
      var keycode = document.all ? event.keyCode : e.which;
      if (keycode == 13) {
        that.handleSubmit("formCustom");
      }
    };
  },
  data() {
    const checkChinese = (rule, value, callback) => {
      if (value) {
        if (/[\u4E00-\u9FA5]/g.test(value)) {
          callback(new Error("登录账号不能包含汉字"));
        } else {
          callback();
        }
      }
      callback();
    };
    return {
      prefixUrl: "http://10.2.122.47:8071",
      formCustom: {
        password: "",
        loginid: ""
      },
      ruleCustom: {
        loginid: [
          { required: true, message: "请输入登录账号", trigger: "blur" },
          { validator: checkChinese, trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入登录密码", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid){
          let data = {
            password:this.formCustom.password.trim(),
            loginid:this.formCustom.loginid.trim()
                      }
          this.axios
            .post("/system/login", JSON.stringify(data), {
              headers: { "Content-Type": "application/json" }
            })
            .then(res => {
                if(res && res.code == '200'){
                  let AmlLtpaToken = res.data.userDetail.token;
                
//                   document.cookie = `AmlLtpaToken=${AmlLtpaToken};`;//开发环境
                    document.cookie = `AmlLtpaToken=${AmlLtpaToken};domain=gf.com.cn`;//线上环境
             
                  this.storage.set("username", res.data.username);
                  this.$store.commit("username", {
                    username:res.data.username,
                  });
                  this.storage.set("firstLoginFlag", true);
                  this.$store.commit("firstLoginFlag", {
                    firstLoginFlag: true
                  });
                  this.getTopMenu();
                }else{
                  this.$Message.error(res.message);
                }
            })
            .catch(error => {
            });
        }
      });
    },
    async getTopMenu() {
      let menurandomBox = [];
      let response = await this.axios.post("/system/menu/getTopMenu");
      if (response.code == "200") {
          menurandomBox = response.data;
          menurandomBox.forEach(element => {
              element.flag2 = false;
              element.flag = false;
          });
          this.storage.set("menuBox", menurandomBox);
          this.$store.commit("menuBox", {
            menuBox: menurandomBox
          });
          this.$router.push({ name: "workCenter" });
      }
   },
    handleReset(name) {
      this.$refs[name].resetFields();
    }
  }
};
</script>
<style scoped>
.screenFull {
  width: 100vw;
  height: 100vh;
  background: url(../assets/logIn.png) repeat;
  position: relative;
}

.siginInTitle {
  width: 100%;
  height: 50px;
  text-align: center;
  margin-bottom: 60px;
}
.siginInTitle img {
  height: 50px;
}
.siginInForm {
  width: 384px;
  height: 504px;
  background-color: white;
  position: absolute;
  top: 50%;
  margin-top: -250px;
  border-radius: 20px;
  right: 16%;
  padding: 45px 30px;
}
.siginInForm .form-content {
  width: 300px;
  margin: 10px auto 0;
}
.btnBox {
  margin-top: 100px;
}
.btnBox button {
  width: 300px;
  height: 50px;
  border-radius: 50px;
  font-size: 16px;
  background-image: linear-gradient(to right, #1c65cb, #1346b0);
}
</style>


